import { heroes, heroImagesPath } from './heroes';

const changeHero = (target) => {
  if (target.classList.contains('hero-card')) {
    const currentHero = document.querySelector('.hero-card-checked');
    const choosenHero = target;

    currentHero.classList.remove('hero-card-checked');
    choosenHero.classList.add('hero-card-checked');
  }
};

class HeroCard {
  constructor() {
    this.heroes = document.querySelector('.hero-cards-wrapper');
  }

  showHeroCards() {
    const fragment = document.createDocumentFragment();
    const template = document.querySelector('.hero-card-temp');

    heroes.forEach((hero) => {
      const cardClone = template.content.cloneNode(true);
      const cardImg = cardClone.querySelector('.hero-card img');
      const cardCaption = cardClone.querySelector('.hero-card figcaption');

      cardImg.setAttribute('src', `${heroImagesPath}/${hero}-card.jpg`);
      cardCaption.textContent = hero;

      fragment.appendChild(cardClone);
    });

    this.heroes.appendChild(fragment);
    this.heroes.firstElementChild.nextElementSibling.classList.add('hero-card-checked');
  }

  addListeners() {
    this.heroes.addEventListener('click', (event) => { changeHero(event.target.parentElement); });

    this.heroes.addEventListener('keyup', (event) => {
      switch (event.code) {
        case 'Enter':
          changeHero(event.target);
          break;
        default:
      }
    });
  }
}

export default HeroCard;
