import './index.html';
import './index.css';
import HeroCard from './HeroCard';

window.onload = () => {
  const heroCard = new HeroCard();
  const playButton = document.querySelector('.login-button-play');
  const nameInput = document.querySelector('input[name="user-name"]');

  heroCard.showHeroCards();
  heroCard.addListeners();

  playButton.addEventListener('click', (event) => {
    const name = encodeURIComponent(nameInput.value);
    const type = encodeURIComponent(document.querySelector('.hero-card-checked').lastElementChild.textContent);

    if (name && nameInput.value.match(/^\s+$/) === null) {
      playButton.href += `?name=${name}&type=${type}`;
    } else {
      event.preventDefault();
      nameInput.focus();
    }
  });
};
